export default {
  computed: {
    showBanner() {
      // alert(this.$store.state.customer.adminAuthorized);
      // Only show if admin is authorized and not in admin panel
      return this.$store.state.customer.adminAuthorized && 
             !this.$route.path.startsWith('/admin-panel');
    }
  }
}
